import { camelCase, map, mapKeys, snakeCase } from "lodash"
import { fromLocalDatabaseProductEntries } from "@/types/LocalDatabaseProductEntry"
import LocalDatabase from "@/state/LocalDatabase"


/**
 * A raw product object, usually stored in the database.
 * Contains the localized data fields for every locale.
 */
export type Product = {
    productRef: string
    barcode: string

    name: string
    nameTranslations: Record<string, string>
    description: string
    descriptionTranslations: Record<string, string>

    category: string
    origin: string
    imageUrl : string

    discount: number
    seasonStart: number
    seasonEnd: number

    packagings: ProductPackage[]
}

export type ProductPackage = {
    weight: number
    name: string
    unitPrice: number
}


/**
 * Returns whether or not two products are the same.
 */
export function productsAreEqual(a: Product, b: Product) {
    return a.productRef === b.productRef
}

/**
 * Returns whether or not a product is seasonal and from the current month.
 */
export function productIsOfCurrentSeason(product: Product) {
    const month = new Date().getMonth() + 1

    return month >= product.seasonStart && month <= product.seasonEnd
}


export function toFrontendProduct(data: Record<string, any>) {
    return <Product> <any> {
        ...mapKeys(data, (_, key) => camelCase(key)),

        packagings: map(data.packagings, productPackage =>
            mapKeys(productPackage, (_, key) => camelCase(key))
        ),
    }
}

export function toBackendProduct(product: Product) {
    return {
        ...mapKeys(product, (_, key) => snakeCase(key)),

        packagings: map(product.packagings, productPackage =>
            mapKeys(productPackage, (_, key) => snakeCase(key))
        ),
    }
}


/**
 * Turns a product into a localized product for a given locale.
 */
export function toLocalizedProduct(product: Product, locale: string): Product {
    return {
        ...product,

        name: product.nameTranslations[locale],
        description: product.descriptionTranslations[locale],
    }
}


export async function getProductFromRef(ref : String,db : LocalDatabase){
   const liste = fromLocalDatabaseProductEntries(await db.products.toArray())
   for(const i in liste){
       if(liste[i].productRef === ref){
           return liste[i]
       }
   }
   return null
}

export async function getUrl(ref : String,db : LocalDatabase){
    const liste = fromLocalDatabaseProductEntries(await db.products.toArray())
    for(const i in liste){
        if(liste[i].productRef === ref){
            return liste[i].imageUrl
        }
    }
    return null
 }