import { fromLocalDatabaseProductEntries } from "@/types/LocalDatabaseProductEntry"
import { Product, productIsOfCurrentSeason, toLocalizedProduct } from "@/types/Product"
import { createStore } from "vuex"
import LocalDatabase from "./LocalDatabase"
import { getCategories, getUser, setCategory, syncProducts } from "./products"
import { fetchRemoteSales } from "@/types/Sales"
import { getTotalPriceCart } from "@/types/Cart"
import { isFavorite } from "@/types/Favorite"


const store = createStore({

    state: () => ({
        db: new LocalDatabase(),
        sideMenuShown: false,

        token : "USMB-PROJECT-SECRET",
        user: {id :-1,username : "",password:"",favori_list:[]},
        item: {
            id: -1,
            package: -1,
            quantity: -1
        },
        hist : [] as any,

        favori : -1,
        total : 0,


        catalog: {
            lastSync: new Date(1970, 0, 0, 0, 0, 0, 0),
            syncInterval: 5,

            loading: false,

            offset: 0,
            perPage: 20,

            products: [] as Product[],

            filters: {
                search: "",
                seasonal: false,
                discount: 0,
                origin: "",
                category: "",
                favorite: false,
            },
        },

        category_list : [],
    }),


    mutations: {
        "sideMenu.open": state => {
            state.sideMenuShown = true
        },

        "sideMenu.close": state => {
            state.sideMenuShown = false
        },


        "catalog.setSyncDateNow": state => {
            state.catalog.lastSync = new Date()
        },

        "catalog.setLoading": (state, loading) => {
            state.catalog.loading = loading
        },

        "catalog.resetProducts": state => {
            state.catalog.offset = 0
            state.catalog.products = []
        },

        "catalog.addProducts": (state, products) => {
            state.catalog.offset += products.length
            state.catalog.products = [...state.catalog.products, ...products]
        },

        "catalog.updateFilters": (state, filters) => {
            state.catalog.filters = {...state.catalog.filters, ...filters}
        },

        "user.getUser": (state, u) => {
            state.user.id = u.id
            state.user.username = u.user
            state.user.password = u.pwd
            state.user.favori_list = u.favori_list
        },


        "hist.getHist": (state, res:any) => {
            state.hist = []
            for(let i = 0 ; i< res.length ; i++){
                const el = res[i]
                state.hist.push({id:i,amount:el.amount,date_order:el.date_order,lines:el.lines,name:el.name})
            }
        },
        "cart.total": (state, total) => {
            state.total = total
        },

        "category.setCategory": (state, category) => {
            state.category_list = category
        },
        "token": (state, token) => {
            state.token = token
        },
    },


    actions: {
        "catalog.syncProducts": syncProducts,
        "catalog.syncProductsIfNeeded": async context => {
            const { state } = context

            if (Math.round((new Date().getTime() - state.catalog.lastSync.getTime()) / (1000 * 60)) >= state.catalog.syncInterval)
                await syncProducts(context)
        },

        "catalog.fetchProducts": async ({ state, commit }) => {
            const filters = state.catalog.filters

            commit("catalog.addProducts",
                fromLocalDatabaseProductEntries(
                    await state.db.products
                        .filter(obj =>
                            obj.product.name.toLocaleLowerCase().indexOf( filters.search.toLowerCase() ) !== -1 &&
                            obj.product.category.toLowerCase().indexOf( filters.category.toLowerCase() ) !== -1 &&
                            obj.product.origin.toLowerCase().indexOf( filters.origin.toLowerCase() ) !== -1 &&
                            (!filters.seasonal || productIsOfCurrentSeason(obj.product)) &&
                            obj.product.discount >= filters.discount &&
                            (!filters.favorite || isFavorite(obj.product.productRef) === filters.favorite)
                        )
                        .offset(state.catalog.offset)
                        .limit(state.catalog.perPage)
                        .toArray()
                )
                    .map(product => toLocalizedProduct(product, "fr"))
            )
        },

        // Updates the given filters and fetches the first page of products
        "catalog.updateFilters": ({ commit, dispatch }, filters) => {
            commit("catalog.updateFilters", filters)
            commit("catalog.resetProducts")

            dispatch("catalog.fetchProducts")
        },

        //cart
        "cart.updateCart": ({ commit }) => {
            commit("cart.total",getTotalPriceCart())
        },

        //connexion
        "user-auth.getUser": getUser,
        "user-auth.connect": async ({ commit, dispatch }, u) => {
            commit("user.getUser", u)

            await dispatch("user-auth.getUser")
            dispatch("cart.fetchCart")
        },
        //hist
        "hist.fetchHist":async ({commit},nb=100,offset=0) =>{
            const sales = await fetchRemoteSales(nb,offset)
            commit("hist.getHist",sales)
        },

        //category
        "category.setCategory": setCategory,
        "category.getCategory": getCategories,
        "category.fetchCategory": ({ dispatch },locale) => {
            dispatch("category.getCategory",locale)
        },

    },

})
export default store
