<template>
  <app-layout>
    <template v-slot:header>
      <catalog-search-bar />
    </template>

    <template v-slot:menu>
      <catalog-side-menu />
    </template>

    <template v-slot:content>
      <div class="catalog" @scroll="onScroll">
        <div v-if="loading">
          <p style="text-align: center;">Synchronisation...</p>
          <progress class="progress is-large is-info" max="100">60%</progress>
          <p style="text-align: center"> {{$t("catalog.loading")}} </p>
        </div>
        <div v-else>
          <p style="text-align: right;">Dernière synchronisation à {{ lastSync.getHours() + ":" + lastSync.getMinutes() }}</p>
        </div>

        <div v-if="products.length > 0">
          <div
            v-bind:class="
              'products ' +
              (mq.tablet || mq.other ? 'products-tablet' : 'products-phone')
            "
          >
            <card-image
              v-for="product in products"
              :key="product.id"

              :product="product"
            />

          </div>
        </div>
        <div v-else>
          <p style="text-align: center">{{$t("catalog.noResult")}}</p>
        </div>
      </div>
    </template>
  </app-layout>
</template>


<script setup>
import { useMq } from "vue3-mq"

const mq = useMq()
</script>


<script>
import AppLayout from "./layout/AppLayout.vue"
import CatalogSearchBar from "./CatalogSearchBar.vue"
import CatalogSideMenu from "./catalog/CatalogSideMenu.vue"
import CardImage from "./CardImage.vue"
import { mapState } from "vuex"


export default {
  inject: ["mq"],
  name: "Catalog",

  components: {
    AppLayout,
    CatalogSearchBar,
    CatalogSideMenu,
    CardImage,
  },

  data() {
    return {
    }
  },

  computed: mapState({
    db: state => state.db,
    loading: state => state.catalog.loading,
    products: state => state.catalog.products,
    lastSync: state => state.catalog.lastSync,
  }),

  methods: {
    onScroll(e) {
      const el = e.target

      if (Math.ceil(el.scrollTop + el.clientHeight) >= el.scrollHeight)
        this.$store.dispatch("catalog.fetchProducts")
    },

    getPackages(product) {
      var data = [];
      for (var i = 0; i < product.packages.length; i++) {
        var quant = product.packages[i].quantity;
        var priceI = product.packages[i].price;
        var pack = {
          quantity: quant,
          quantity_cart: this.findQuantityCart(product, quant),
          price: priceI,
        };
        data.push(pack);
      }
      return data;
    },
    findQuantityCart(product, packag) {
      var qt = 0;
      var cart = this.$store.state.cart.data;
      for (var i = 0; i < cart.length; i++) {
        if (cart[i].id === product.id && cart[i].package === packag) {
          qt = cart[i].quantity;
          break;
        }
      }
      return qt;
    },
  },

    mounted() {
        this.$store.dispatch("catalog.syncProductsIfNeeded")
        this.$store.dispatch("catalog.fetchProducts")
    },
};
</script>


<style scoped>
.catalog {
  width: 100%;
  height: 100%;

  overflow-y: scroll;
}

.products {
  overflow-y : hidden;
  display: grid;
  gap: 2vw;
}

.products-phone {
  grid-template-columns: 1fr;
}

.products-tablet {
  grid-template-columns: 1fr 1fr;
}
</style>
