<template>
    <app-layout>
        <template v-slot:header>
            <cart-header-bar  @eventname="refresh" />
        </template>

        <template v-slot:content>
            <cart-catalog :refresh="refreshCart"  @eventname="refresh" />
        </template>
    </app-layout>
</template>


<script>
    import AppLayout from "../layout/AppLayout.vue"
    import CartHeaderBar from "./CartHeaderBar.vue"
    import CartCatalog from "./CartCatalog.vue"


    export default {
        name: "Cart",

        components: {
            AppLayout,
            CartHeaderBar,
            CartCatalog,
        },  
        data() {
            return {refreshCart : false
            }
        },
        methods: {
            refresh(variable) {
                this.refreshCart = variable
            }
        }
    }
</script>


