<template>
  <h4 class="is-size-4 section-title">{{ $t("sidemenu.filters") }}</h4>

  <div class="side-menu-bar">
    <div
      :class="'button egalize ' + (seasonal ? 'selected' : 'not-selected')"
      @click="onSeasonalClick"
    >
      {{ $t("catalog.seasonal") }}
    </div>

    <p class="menu-label">{{ $t("sidemenu.discount") }}</p>
    <div class="select egalize">
      <select id="discount" class="has-text-centered" @change="onDiscountChange">
        <option :selected="discount === 0" value="0">
          {{ $t("sidemenu.filtersPlaceHolder") }}
        </option>
        <option :selected="discount === 20" value="20">20%</option>
        <option :selected="discount === 40" value="40">40%</option>
        <option :selected="discount === 60" value="60">60%</option>
        <option :selected="discount === 80" value="80">80%</option>
      </select>
    </div>
    <div class="menu-label">{{ $t("sidemenu.origin") }}</div>
    <div class="menu-label">
      <input
        class="search-input input egalize has-text-centered"
        type="text"
        :placeholder="$t('catalog.hintResearch')"
        :value="origin"
        @change="onOriginChange"
      />
    </div>
    <div class="menu-label">{{ $t("sidemenu.category") }}</div>
    <div class="select egalize">
      <select id="category" class="totalite has-text-centered" @change="onCategoryChange">
      </select>
    </div>
  </div>

  <div class="control has-icons-left languages bottom">
    <div class="select">
      <select id="select" @change="change_language($event)">
        <option value="en-EN">{{$t("language.en")}}</option>
        <option value="fr-FR">{{$t("language.fr")}}</option>
        <option value="de-DE">{{$t("language.de")}}</option>
        <option value="it-IT">{{$t("language.it")}}</option>
      </select>
    </div>
    <span class="icon is-left">
      <span class="material-icons-outlined"> language </span>
    </span>
    <div class="button ml-4" @click="syncProduct()"><span class="material-icons"> refresh </span></div>
  </div>

</template>


<script>
import { mapState } from "vuex";

export default {
  name: "CatalogSideMenu",

  computed: mapState({
    seasonal: (state) => state.catalog.filters.seasonal,
    origin: (state) => state.catalog.filters.origin,
    discount: (state) => state.catalog.filters.discount,
    category: (state) => state.catalog.filters.category,
  }),

  mounted() {
    this.$store.dispatch("category.fetchCategory",{"locale" : this.$i18n.locale,"force" : false});
    var index = 0;
    switch(navigator.language){
      case"fr-FR":index = 1;break;
      case"de-DE":index = 2;break;
      case"it-IT":index = 3;break;
      default:index = 0;break;
    }
    document.getElementById("select").selectedIndex = index;
  },

  methods: {
    onSeasonalClick() {
      this.$store.dispatch("catalog.updateFilters", {
        seasonal: !this.seasonal,
      });
    },

    onOriginChange(e) {
      this.$store.dispatch("catalog.updateFilters", { origin: e.target.value });
    },

    show_value() {
      const slider = document.getElementById("discount");
      const output = document.getElementById("valueSlider");
      output.innerHTML = slider.value;
    },

    onDiscountChange(e) {
      this.$store.dispatch("catalog.updateFilters", {
        discount: parseInt(e.target.value),
      });
    },
    change_language(event){
       this.$root.$i18n.locale = event.target.value
       this.$store.dispatch("category.fetchCategory",{"locale" : this.$i18n.locale,"force" : true})
    },

    onCategoryChange(e) {
      this.$store.dispatch("catalog.updateFilters", {
        category: e.target.value,
      });
    },
    syncProduct(){
      this.$store.dispatch("catalog.syncProductsIfNeeded")
    }
  },
};
</script>


<style scoped>
.section-title {
  text-align: center;
  border-top: 1px solid #20ec65;
  border-bottom: 1px solid #20ec65;
}
.side-menu-bar {
  padding-left: 5px;
  padding-right: 5px;
}
.egalize{
  width: 70%;
}
#discount,.totalite{
  width:100%;
}
#valueSlider {
  text-align: center;
  width: 20%;
}

.not-selected {
  background: white;
}
.selected {
  background: rgb(81, 207, 91);
}
.section-title {
  text-align: center;
  border-top: 1px solid #20ec65;
  border-bottom: 1px solid #20ec65;
}
.side-menu-bar {
  padding-top: 10px;
  padding-left: 5px;
  text-align: center;
}
.languages{
 position:absolute;
 margin:15px;
 bottom:0;
 left:0;
}
</style>
