<template>
  <menu-button />

  <span class="tag price bar-element">
    {{ $t("cart.total") }} : {{ total.toFixed(2) }} CHF
  </span>
  <span class="bar-element">
    <span class="tag  bar-element erreur_msg has-background-danger has-text-white is-size-6 mr-4 unactive"></span>
    <button class="button bar-element" @click="send_cart()" >
      {{ $t("cart.buy") }}
    </button>
  </span>
</template>


<script>
import { mapState } from "vuex"
import MenuButton from "../layout/MenuButton.vue"
import { sendRemoteSales } from "@/types/Sales"
import { resetCart,isCartEmpty } from '@/types/Cart';

export default {
  name: "CartHeaderBar",

  components: {
    MenuButton,
  },

  computed: mapState({
    total: (state) => state.total,
  }),
  methods:{
    async send_cart(){
      this.isDisabled = true
      if(!isCartEmpty()){
        const res = await sendRemoteSales()
        if(res.status == 200){
          resetCart()
          this.$store.dispatch("cart.updateCart")
          this.$emit('eventname', true)
          this.isDisabled = false
        }else{
          this.showMessage(this.$t("cart.erreur_commande"))
        }
      }else{
        this.showMessage(this.$t("cart.empty_cart"))
      }
    },
    showMessage(message){
        document.getElementsByClassName("erreur_msg")[0].innerHTML = message
        document.getElementsByClassName("erreur_msg")[0].classList.remove("unactive")
        setTimeout(this.closeMessage, 5000)
    },
    closeMessage(){
      document.getElementsByClassName("erreur_msg")[0].classList.add("unactive")
      this.isDisabled = false
    }
  }
};
</script>


<style scoped>
.unactive{
  display:none;
}
.button {
  background-color: #d2ffc3;
  border-color: #20ec65;
}
.price {
  background-color: #f3970c;
  border-color: #b4710c;
  color: black;
}

.bar-element {
  height: 50px;
}
</style>
