import { Dictionary} from "lodash"
import {Product} from "./Product"

export type Favorites = {
    favorite : Dictionary<Product>
}


export function addFavorite(product: Product){
    const str = localStorage.getItem("favorites")
    let favorites = null
    if(str!=null){
        favorites = JSON.parse(str)
    }
    if(favorites != null){
        favorites.favorites[product.productRef] = product
    }
    localStorage.setItem("favorites", JSON.stringify(favorites))
}

export function deleteFavorite(product: Product){
    const str = localStorage.getItem("favorites")
    let favorites = null
    if(str!=null){
        favorites = JSON.parse(str)
    }
    if(favorites != null){
        delete favorites.favorites[product.productRef]
    }
    localStorage.setItem("favorites", JSON.stringify(favorites))
}

export function changeFavorite(product: Product){
    const str = localStorage.getItem("favorites")
    let favorites = null
    if(str!=null){
        favorites = JSON.parse(str)
    }
    if(favorites != null){
        if(favorites.favorites[""+product.productRef] != null){
            deleteFavorite(product)
        }else{
            addFavorite(product)
        }
    }
}

export function getFavorites(){
    return JSON.parse(localStorage.get("favorites"))
}

export function isFavorite(product: String){
    const str = localStorage.getItem("favorites")
    let favorites = null
    if(str!=null){
        favorites = JSON.parse(str)
    }
    return favorites.favorites[""+product] != null
}