<template>
  <div class="list-element">
    <div class="empty_msg" v-if="isEmpty">
      {{ $t("cart.empty_cart") }}
    </div>
    <div
      v-bind:class="
        'element ' +
        (mq.tablet || mq.other ? 'element-tablet' : 'element-phone')
      "
      v-for="element in cart"
      :key="element.id"
    >
      <cart-element
        v-bind:class="
          'product ' +
          (mq.tablet || mq.other ? 'product-tablet' : 'product-phone')
        "
        :name="element.package.name"
        :quantity="element.quantity"
        :elementPackage="element.package.weight"
        :pricePerKg="element.package.unitPrice"
        :reference="element.ref"
      />

      <div
        v-bind:class="
          'field has-addons add-tool ' +
          (mq.tablet || mq.other ? 'add-tool-tablet' : 'add-tool-phone')
        "
      >
        <span class="control">
          <button
            id="add"
            v-bind:class="
              'is-primary is-rounded button' +
              (mq.tablet || mq.other ? ' ' : ' phone')
            "
            @click="rmQuantity(element.ref, element.package,element.id)"
          >
            <span class="material-icons-outlined"> remove </span>
          </button>
        </span>

        <input
          v-bind:class="
            'control tag ' + element.id + (mq.tablet || mq.other ? ' ' : '-phone')
          "
          v-model="element.quantity"
          v-on:keypress.enter="
            setQuantity(element.ref, element.package, element.quantity,element.id)
          "
        />

        <span class="control">
          <button
            v-bind:class="
              'is-primary is-rounded button' +
              (mq.tablet || mq.other ? ' ' : ' phone')
            "
            @click="addQuantity(element.ref, element.package,element.id)"
          >
            <span class="material-icons-outlined"> add </span>
          </button>
        </span>
      </div>
    </div>
  </div>
</template>


<script setup>
import { useMq } from "vue3-mq"

const mq = useMq()
</script>


<script>
import CartElement from "./CartElement.vue"
import {isCartEmpty,addToCart, getPackagesValue,setCartItem,getCart} from "../../types/Cart"



export default {
  inject: ["mq"],
  name: "CartCatalog",

  components: {
    CartElement,
  }, 
  props: {
    refresh: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      cart: [],
      isEmpty : true
    }
  },
   watch: {
    refresh() {
      this.cart = getCart()
      this.isEmpty = isCartEmpty()
      this.$emit('eventname', false)
    }
  },

  mounted() {
    this.cart = getCart()
      console.log(this.cart)
    this.isEmpty = isCartEmpty()
    this.$store.dispatch("cart.updateCart")
  },
  methods: {
    getCart(){
      return getCart()
    },
    putValueInInput(id){
        this.cart = getCart()
        this.isEmpty = isCartEmpty()
        document.getElementsByClassName(id)[0].value = getPackagesValue(id)
        this.$store.dispatch("cart.updateCart")
      },
     addQuantity(id, pack,id_class) {
      addToCart({package : pack,quantity : 1},id)
      this.putValueInInput(id_class)
    },

    rmQuantity(id, pack,id_class) {
      addToCart({package : pack,quantity : -1},id)
      this.putValueInInput(id_class)
    },

    setQuantity(id, pack,qt,id_class) {
      const item = {package : pack,quantity : parseInt(qt)};
      setCartItem(id,item)
      this.putValueInInput(id_class)
    },
    isCartEmpty(){
      return isCartEmpty()
    }
  },
};
</script>


<style scoped>
.element {
  background-color: #d2ffc3;
  padding: 20px;
  border-radius: 15px;

  display: grid;
  gap: 1rem;
  justify-content: flex-start;
  align-content: center;

  flex-direction: column;
  margin-top: 5px;
  margin-bottom: 5px;
}
.element-phone {
  grid-template-columns: 1fr;
}
.element-tablet {
  grid-template-columns: 1fr 1fr;
}

.product {
  height: 100%;
  width: 100%;
  border-radius: 15px;
}
.add-tool {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.add-tool-phone {
  align-items: center;
  justify-content: center;
}

.button {
  height: 6vw;
  width: 6vw;
}
.tag {
  height: 6vw;
  width: 12vw;
  font-size: 3vw;
  text-align: center;
}
.empty_msg {
  text-align: center;
}
.phone {
  height: 12vw;
  width: 12vw;
}
.tag-phone {
  height: 12vw;
  width: 24vw;
  font-size: 6vw;
}
</style>
