<template>
  <!-- <div>
    {{ this.$route.query.barcode }}
  </div>
  <div>
    <img :src="info.data.product.image_url" alt="img" />
  </div> -->
  <app-layout>
    <template v-slot:header>
      <product-bar v-if="product != null" :productName="product.name"/>
    </template>

    <template v-slot:content>
      <div v-if="info.image_url" class="img-wrapper">
        <Popper arrow>
            <template #content>
                <Modal :packagings="product.packagings" :id_item="product.productRef" :qt_item="0" />
            </template>
            <img :src="product.imageUrl" alt="image produit"/>
        </Popper>


        <h1 class="title">Ecoscore</h1>
        <figure class="image is-96x96">
          <p v-if="info.ecoscore_grade == 'not-applicable'"> Information non disponible</p>
          <img v-else
            :src="'./images/assets/ecoscore/'+info.ecoscore_grade.toUpperCase()+'.png'"
            alt="ecoscore"
          />
        </figure>
        <h1 class="title">Nutriscore</h1>

        <figure class="image is-96x96">
          <img
            :src="'./images/assets/nutriscore/'+info.nutriscore_grade.toUpperCase()+'.jpg'"
            alt="ecoscore"
          />
        </figure>
        <h1 class="title">Saisonalité</h1>
        <table class="table">
          <thead>
            <tr>
              <th v-if="(product.seasonStart>=1 && product.seasonEnd <= 1)|| (product.seasonStart<=1 && product.seasonEnd >= 1)"><abbr>Janvier</abbr></th>
              <th v-else class="is-selected"><abbr>Janvier</abbr></th>
              <th v-if="(product.seasonStart>=2 && product.seasonEnd <= 2)|| (product.seasonStart<=2 && product.seasonEnd >= 2)"><abbr>Février</abbr></th>
              <th v-else class="is-selected"><abbr>Février</abbr></th>
              <th v-if="(product.seasonStart>=3 && product.seasonEnd <=3)|| (product.seasonStart<=3 && product.seasonEnd >= 3)"><abbr>Mars</abbr></th>
              <th v-else class="is-selected"><abbr>Mars</abbr></th>
              <th v-if="(product.seasonStart>=4 && product.seasonEnd <= 4)|| (product.seasonStart<=4 && product.seasonEnd >= 4)"><abbr>Avril</abbr></th>
              <th v-else class="is-selected"><abbr>Avril</abbr></th>
              <th v-if="(product.seasonStart>=5 && product.seasonEnd <= 5)|| (product.seasonStart<=5 && product.seasonEnd >= 5)"><abbr>Mai</abbr></th>
              <th v-else class="is-selected"><abbr>Mai</abbr></th>
              <th v-if="(product.seasonStart>=6 && product.seasonEnd <= 6)|| (product.seasonStart<=6 && product.seasonEnd >= 6)"><abbr>Juin</abbr></th>
              <th v-else class="is-selected"><abbr>Juin</abbr></th>
              <th v-if="(product.seasonStart>=7 && product.seasonEnd <= 7)|| (product.seasonStart<=7 && product.seasonEnd >= 7)"><abbr>Juillet</abbr></th>
              <th v-else class="is-selected"><abbr>Juillet</abbr></th>
              <th v-if="(product.seasonStart>=8 && product.seasonEnd <= 8)|| (product.seasonStart<=8 && product.seasonEnd >= 8)"><abbr>Aout</abbr></th>
              <th v-else class="is-selected"><abbr>Aout</abbr></th>
              <th v-if="(product.seasonStart>=9 && product.seasonEnd <= 9)|| (product.seasonStart<=9 && product.seasonEnd >= 9)"><abbr>Septembre</abbr></th>
              <th v-else class="is-selected"><abbr>Septembre</abbr></th>
              <th v-if="(product.seasonStart>=10 && product.seasonEnd <= 10)|| (product.seasonStart<=10 && product.seasonEnd >= 10)"><abbr>Octobre</abbr></th>
              <th v-else class="is-selected"><abbr>Octobre</abbr></th>
              <th v-if="(product.seasonStart>=11 && product.seasonEnd <= 11)|| (product.seasonStart<=11 && product.seasonEnd >= 11)"><abbr>Novembre</abbr></th>
              <th v-else class="is-selected"><abbr>Novembre</abbr></th>
              <th v-if="(product.seasonStart>=12 && product.seasonEnd <= 12)|| (product.seasonStart<=12 && product.seasonEnd >= 12)"><abbr>Décembre</abbr></th>
              <th v-else class="is-selected"><abbr>Décembre</abbr></th>
            </tr>
          </thead>
        </table>


        <h1 class="title">Valeurs nutritionnels</h1>

          <Bar
            :chart-options="chartOptions"
            :chart-data="chartData"
            :chart-id="chartId"
            :dataset-id-key="datasetIdKey"
            :plugins="plugins"
            :css-classes="cssClasses"
            :styles="styles"
            :width="width"
            :height="height"
          />

      </div>
    </template>
  </app-layout>
</template>

<script>
import { mapState } from "vuex"
import axios from "axios"
import AppLayout from "../layout/AppLayout.vue"
import ProductBar from "./ProductBar.vue"
import { getProductFromRef } from "@/types/Product"
import { Bar } from 'vue-chartjs'
import Modal from "@/components/Modal.vue"
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'

import Popper from "vue3-popper"

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)

export default {
  name: "Product",
  computed: mapState({
    db: (state) => state.db,
  }),
  components: { AppLayout, ProductBar, Bar, Modal , Popper},
    props: {
    chartId: {
      type: String,
      default: 'bar-chart'
    },
    datasetIdKey: {
      type: String,
      default: 'label'
    },
    width: {
      type: Number,
      default: 20
    },
    height: {
      type: Number,
      default: 20
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object,
      default: () => {}
    },
    plugins: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      product : null,
      info: {},
      chartData: {
        labels: [ 'Sucre', 'Calcium', 'lipide' ],
        datasets: [ { data: [10, 15, 5] } ]
      },
      chartOptions: {
        responsive: true
      }
    };
  },
  async mounted(){
     this.product = await getProductFromRef(this.$route.params.ref,this.db)
  },
  created() {
    axios
      .get(
        "https://world.openfoodfacts.org/api/v0/product/" +
          this.$route.params.barcode +
          ".json"
      )
      .then((response) => {
        this.info = response.data.product;
        console.log(this.info);
        });
    },
};
</script>

