<template>
<div class="elementCart">

  <div class="name">
    {{ removeFirstWord(name) }}
  </div>

  <div class="pack">
      {{ packag }} {{ price.toFixed(2) }} CHF x {{ quantity }}
  </div>

  <div class="price">
  {{ (price.toFixed(2) * quantity).toFixed(2) }} CHF
  </div>

</div>
</template>


<script>
export default {
  name: "CartElement",

  props: {
    name: {
      type: String,
      required: true,
    },
    packag: {
      type: String,
      required: true,
    },
    quantity: {
      type: String,
      required: true,
    },
    price: {
      type: String,
      required: true,
    },
  },
  methods:{
    removeFirstWord(str) {
        const indexOfSpace = str.indexOf(' ');

        if (indexOfSpace === -1) {
          return '';
        }

        return str.substring(indexOfSpace + 1);
      }
  }
};
</script>


<style scoped>

.elementCart{
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: white;
}
.name{
  justify-items: left;
  margin-left: 5px;
  padding-right:5px;
}
.price{
  margin-left: auto;
  margin-right: 5px;
  justify-items: right;
}
.pack{
  justify-items: center;
}


</style>
