import api from "../api"
import {getCartSale,getTotalPriceCart, isCartEmpty} from "./Cart"

export type Sale = {
    amount: Number,
    date_order: String,
    lines: Array<SaleProduct>,
    name: String
}

export type SaleProduct = {
    product_code: string,
    qty: 0,
    description: String,
    packaging: String,
    price_unit: Number
}

export async function fetchRemoteSales(perPage: number = 100, offset: number = 0): Promise<Array<Sale>> {
    const { data } = await api.get("/sale/search?number=" + encodeURIComponent(perPage) + "&offset=" + encodeURIComponent(offset))

    if (!data.sales) throw "invalid data: missing 'products' key"
    if (data.sales.length === 0) return []
    return data.sales
}

export async function sendRemoteSales(): Promise<any> {
    const cart = getCartSale()
    const price = getTotalPriceCart()
    return await api.post("/sale/create",
    {
        amount: price,
        date_order: new Date().toISOString().slice(0, 10),
        lines: cart
    }).catch(function () {
        return {status:500}
    })
}