import axios from "axios"
import { differenceWith, intersectionWith, map } from "lodash"
import { ActionContext } from "vuex"
import api from "../api"
import { fromLocalDatabaseProductEntries, toLocalDatabaseProductEntries } from "../types/LocalDatabaseProductEntry"
import { Product, productsAreEqual, toFrontendProduct } from "../types/Product"
import LocalDatabase from "./LocalDatabase"


/**
 * Fetches all the products that are available on the server.
 */
export async function fetchRemoteProducts(perPage: number = 100, offset: number = 0): Promise<Product[]> {
    const { data } = await api.get("/stock/search?number=" + encodeURIComponent(perPage) + "&offset=" + encodeURIComponent(offset))
    
    if (!data.products) throw "invalid data: missing 'products' key"
    if (data.products.length === 0) return []

    return [
        ...map(data.products, product => toFrontendProduct(product)),
        ...await fetchRemoteProducts(perPage, offset + perPage)
    ]
}

/**
 * Fetches the product list from the server and updates the local database.
 */
export async function syncProducts({ state, commit, dispatch }: ActionContext<any, any>) {
    const { db }: { db: LocalDatabase } = state
    commit("catalog.setLoading", true)

    try {
        const [remoteProducts, localEntries] = await Promise.all([
            fetchRemoteProducts(),
            db.products.toArray(),
        ])

        const localProducts = fromLocalDatabaseProductEntries(localEntries)

        await Promise.all([
            db.products.bulkAdd(
                toLocalDatabaseProductEntries(
                    differenceWith(remoteProducts, localProducts, productsAreEqual)
                )
            ),

            db.products.bulkPut(
                toLocalDatabaseProductEntries(
                    intersectionWith(remoteProducts, localProducts, productsAreEqual)
                )
            ),

            db.products.bulkDelete(
                map(
                    differenceWith(localProducts, remoteProducts, productsAreEqual),
                    product => product.productRef,
                )
            ),
        ])

        commit("catalog.setSyncDateNow")

        commit("catalog.resetProducts")
        dispatch("catalog.fetchProducts")
    }
    catch (e) {
        console.error("Could not synchronize product database: " + e)
    }

    commit("catalog.setLoading", false)
}

 export async function getUser({state, commit}: ActionContext<any, any>) {
    await axios
        .get("http://localhost:3000/user-auth"
            + "?username=" + encodeURIComponent(state.user.username)
            + "&password=" + encodeURIComponent(state.user.password))
        .then(res => {
            if (res.data.user === null) {
                commit("user.getUser", { id: -1, user: null, pwd: null,favori_list : [] })
            } else {
                commit("user.getUser", { id: res.data.user.id, user: res.data.user.user, pwd: "" ,favori_list : res.data.user.favori_list})
            }
        })
}

export function fetchHist({state, commit}: ActionContext<any, any>) {
    axios
        .get("http://localhost:3000/hist"
            + "?idUser=" + encodeURIComponent(state.user.id))
        .then(res => {
            commit("hist.getHist", res.data.hist)
        })
}

export function getCategories({state,commit,dispatch}: ActionContext<any, any>,locale : any) {
    if(!locale["force"] && state.category_list.length > 0){
        dispatch("category.setCategory")
    }else{
        api.get('/stock/categories')
            .then(res => {
                locale = locale.locale.split("-")[0]
                const category = [] as any
                for(let i = 0 ; i < res.data.categories.length ; i++){
                    const name = res.data.categories[i].name_translations[locale["locale"]]
                    //case if the language doesn't exist (english for example)
                    if(name === undefined){
                        const key = Object.keys(res.data.categories[i].name_translations)[0]
                        category.push( res.data.categories[i].name_translations[key])
                    }else{
                        category.push(name)
                    }
                }
                commit("category.setCategory",category)
                dispatch("category.setCategory")
            })
    }
}

export function setCategory({state}: ActionContext<any, any>){
    document.getElementById("category")!.innerHTML = ""
    for(let i = 0 ; i < state.category_list.length ; i++){
        const name = state.category_list[i]
        document.getElementById("category")!.innerHTML += `<option :selected="category === '`+ name +`'" value='`+ name +`'>`+ name +`</option>`
    }
}