<script>

import { addToCart,getPackages, setCartItem } from "../types/Cart"

export default {
  name: "Modal",
  props:{
    packagings:{
      type: Array,
      required:true,
    },
    id_item: {
      type: String,
      required:true,
    }
  },
  methods: {
            putValueInInput(id_item){
              const p = getPackages(id_item)
              let el = document.getElementsByClassName(id_item)
                for(let i in el){
                    if(!Number.isInteger(el[i])){
                      el[i].value = 0
                    }
                }
              if(p!= undefined){
                for(let i in p){
                  if(document.getElementsByClassName(id_item+p[i].package.weight)[0] != undefined){
                    if(p[i].quantity > 0 ){
                      document.getElementsByClassName(id_item+p[i].package.weight)[0].value = p[i].quantity
                    }else{
                      document.getElementsByClassName(id_item+p[i].package.weight)[0].value = 0
                    }
                  }
                }
              }
            },
            addQuantity(id, pack) {
              addToCart({package : pack,quantity : 1},id)
              this.putValueInInput(id)
            },

            rmQuantity(id, pack) {
              addToCart({package : pack,quantity : -1},id)
              this.putValueInInput(id)
            },

            setQuantity(id, pack) {
              const qt = document.getElementsByClassName(id)[0].value;
              const item = {package : pack,quantity : parseInt(qt)};
              setCartItem(id,item)
              this.putValueInInput(id)
            },
            showPackage(quantity){
                var res ="";
                if(quantity<1000){
                  res = quantity + "g"
                }else{
                  res = ""+quantity/1000 +"kg";
                }
                return res;
            },
    
        },mounted(){
          this.putValueInInput(this.id_item)
        }
};
</script>

<template>

      <table class="table">
        <thead>
          <tr>
            <th>
              <h2 class="title">{{$t("packages.quantity")}}</h2>
            </th>
            
            <th>
                <h2 class="title">{{$t("packages.price")}}</h2>
            </th>
          </tr>
        </thead>

        <tbody>
          <tr class="ligne" v-for="packageI in packagings" :key="packageI.id">

            <td  class="quantityValue">
              <h3 class="prix">{{showPackage(packageI.weight)}}</h3>
            </td>

            <td  class="priceValue">
              <h3>{{(packageI.unitPrice).toFixed(2)}} CHF</h3>
            </td>

            <td class="buttonsValue" >
              <span id="buttonList" class="field has-addons">

                <span class="control">
                  <button id="boutonMoins" class="button is-primary is-rounded" @click="rmQuantity(id_item, packageI)">
                    <span class="material-icons">remove</span>
                  </button>
                </span>
                
                <span id="inputNb" class="control">
                  <input id="inputPopup" type="number"
                  :class="'input is-primary '+ id_item + ' ' + id_item + packageI.weight"
                  placeholder="0"
                  v-on:keypress.enter="setQuantity(id_item, packageI)">

                </span>

                <span class="control">
                  <button id="boutonPlus" class="button is-primary is-rounded" @click="addQuantity(id_item,packageI)">
                    <span class="material-icons">add</span>
                  </button>
                </span>
              </span>
            </td>
          </tr>
        </tbody>
      </table>
</template>

<style scoped>

  #boutonPlus,#boutonMoins{
    width: 15px !important;
  }

  .buttonsValue{
    width: fit-content !important;
  }

  .prix{
    text-align: center;
  }

  input{
    min-width: 35px;
    max-width: 80px;
    text-align: center;
  }

  input[type=number] {
  -moz-appearance: textfield;
}
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

</style>