<template>
  <div class="card">
    <img class="img_product" :src="url" alt="image" />
    <div class="custom-card-content">
      <div class="name">{{ name }}</div>
      <div class="price">{{ pricePerKg.toFixed(2) }}CHF/{{ elementPackage }}G</div>
    </div>
  </div>
</template>


<script>
import { mapState } from "vuex"
import {getUrl} from "@/types/Product"
export default {
  name: "CartElement",

  computed: mapState({
    db: (state) => state.db,
  }),
  data() {
    return {
      url : "",
    }
  },
  props: {
    name: {
      type: String,
      required: true,
    },
    quantity: {
      type: String,
      required: true,
    },
    elementPackage: {
      type: String,
      required: true,
    },
    pricePerKg: {
      type: String,
      required: true,
    },
    reference: {
      type: String,
      required: true,
    },
  },
  async mounted(){
    this.url = await getUrl(this.reference,this.db)
  }
}; 
</script>


<style scoped>
.card {
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 9;
  overflow: hidden;
}

.custom-card-content {
  position: absolute;
  display: inline-flex;
  bottom: 0;
  left: 0;
  padding-left: 10px;
  padding-right: 10px;
  width: 100%;
  height: fit-content;
  background-color: rgba(196, 196, 196, 0.8);
}
.img_product {
  width:100%;
  height: 100%;
}

.name {
  vertical-align: middle;
  line-height: 5vh;
  width: 50%;
  text-align: left;
}
.price {
  vertical-align: middle;
  line-height: 5vh;
  width: 50%;
  text-align: right;
}
</style>
