import { Dictionary, omit } from "lodash"
import {ProductPackage} from "./Product"

export type Cart = {
    product_list : Dictionary<Array<Cart_Item>>
}

export type Cart_Item = {
    package : ProductPackage
    quantity : number
}

export function addToCart(cart_item: Cart_Item, ref: String,) {
    const cartString = localStorage.getItem("cart")
    if (cartString != null) {
        const cart = JSON.parse(cartString)
        const item = getCartItem(ref,cart_item) as any
        if(cart.product_list[""+ref]!=null){
            if(isPackageInCart(cart.product_list[""+ref],cart_item)){
                for(const i in cart.product_list[""+ref]){
                    if(cart.product_list[""+ref][i].package.weight == cart_item.package.weight){
                        cart.product_list[""+ref][i].quantity += cart_item.quantity
                        if(cart.product_list[""+ref][i].quantity <= 0){
                            cart.product_list[""+ref].splice(cart.product_list[""+ref].indexOf(cart.product_list[""+ref][i]),1)
                            if(cart.product_list[""+ref].length == 0){
                                cart.product_list = omit(cart.product_list, [""+ref])
                            }
                        }
                        break
                    }
                }
            }else if(cart_item.quantity > 0){
                cart.product_list[""+ref].push(cart_item)
            }
        }else{
                cart.product_list[""+ref] = [cart_item]
        }
        localStorage.setItem("cart", JSON.stringify(cart))
    }
}

export function isPackageInCart(cart:any,p:Cart_Item):boolean{
    for(const i in cart){
        if(cart[i].package.weight == p.package.weight){
            return true
        }
    }
    return false
}

export function getCartItem(ref: String, cart_item:Cart_Item) {
    const cartString = localStorage.getItem("cart")
    if (cartString != null) {
        const cart = JSON.parse(cartString)
        const prod = cart.product_list[""+ref]
        if(prod != null){
            prod.forEach((element:Cart_Item) => {
                if(element.package.weight == cart_item.package.weight) {
                    return element
                }
            });
        }
    }
    return null
}

export function setCartItem(ref: String, cart_item:Cart_Item) {
    const cartString = localStorage.getItem("cart")
    if (cartString != null) {
        const cart = JSON.parse(cartString)
        const item = getCartItem(ref,cart_item) as any
        if(cart.product_list[""+ref]!=null){
            if(isPackageInCart(cart.product_list[""+ref],cart_item)){
                for(const i in cart.product_list[""+ref]){
                    if(cart.product_list[""+ref][i].package.weight == cart_item.package.weight){
                        cart.product_list[""+ref][i].quantity = cart_item.quantity
                        if(cart.product_list[""+ref][i].quantity <=0){
                            cart.product_list[""+ref].splice(cart.product_list[""+ref].indexOf(item),1)
                            if(cart.product_list[""+ref].length == 0){
                                cart.product_list = omit(cart.product_list, [""+ref])
                            }
                        }
                        break
                    }
                }
            }else if(cart_item.quantity > 0){
                cart.product_list[""+ref].push(cart_item)
            }
        }else{
            cart.product_list[""+ref] = [cart_item]
        }
        localStorage.setItem("cart", JSON.stringify(cart))
    }
}

export function getPackages(ref:String){
    const cartString = localStorage.getItem("cart")
    if(cartString !=null){
        const cart = JSON.parse(cartString)
        return cart.product_list[""+ref]
    }
    return null
}

export function getCart(){
    const cartString = localStorage.getItem("cart")
    if(cartString !=null){
        const cart =  JSON.parse(cartString)
        const res = [] as any
        let id = 0
        for(const i in cart.product_list){
            for(const j in cart.product_list[i]){
                if(cart.product_list[i][j].quantity > 0){
                    res.push({package:cart.product_list[i][j].package,quantity:cart.product_list[i][j].quantity,ref:i,id:id++})
                }
            }
        }
        return res
    }
    return null
}
export function getPackagesValue(id:any){
    const cart = getCart()
    if(cart[id] != undefined){
        return cart[id].quantity
    }else{
        return 0
    }
}

export function getTotalPriceCart(){
    const cartString = localStorage.getItem("cart")
    if (cartString != null) {
        let total = 0;
        const cart = JSON.parse(cartString)
        for(const i in cart.product_list){
            for(const j in cart.product_list[i]){
                if(cart.product_list[i][j].quantity > 0){
                    total += cart.product_list[i][j].quantity * cart.product_list[i][j].package.unitPrice
                }
            }
        }
        return total;
    }
    return 0;
}

export function isCartEmpty(){
    return getTotalPriceCart() == 0
}

export function getCartSale(){
    const cart = getCart()
    const res = []
    for(const i in cart){
        const item : any = {
            product_code: cart[i].ref,
            qty: cart[i].quantity,
            description: "",
            packaging:"",
            price_unit: ""
        }
        for(const j in cart[i].package){
            let ref = "description"
            if(j == "unitPrice"){
                ref = "price_unit"
            }else if( j == "name"){
                ref = "packaging"
            }
            item[ref] = cart[i].package[j]+""
        }
        res.push(item)
    }
    return res
}

export function resetCart(){
    localStorage.setItem("cart",JSON.stringify({product_list :{}}))
}