import { map } from "lodash"
import { Product } from "./Product"


/**
 * A product that can be stored in the local database.
 */
export type LocalDatabaseProductEntry = {
    productRef: string
    product: Product
}


/**
 * Creates a local database entry from a product.
 */
export function toLocalDatabaseProductEntry(product: Product): LocalDatabaseProductEntry {
    return {
        productRef: product.productRef,
        product,
    }
}

/**
 * Creates an array of local database entries from an array of products.
 */
export function toLocalDatabaseProductEntries(products: Product[]) {
    return map(products, product => toLocalDatabaseProductEntry(product))
}


/**
 * Creates a product from a local database entry.
 */
export function fromLocalDatabaseProductEntry(entry: LocalDatabaseProductEntry) {
    return entry.product
}

/**
 * Creates an array of products from an array of database entries.
 */
export function fromLocalDatabaseProductEntries(entries: LocalDatabaseProductEntry[]) {
    return map(entries, entry => fromLocalDatabaseProductEntry(entry))
}
